<template>
	<div>
		<el-form-item label="名称">
			<el-input v-model="data.name"></el-input>
		</el-form-item>
		<el-form-item label="提示">
			<el-input v-model="data.tips"></el-input>
		</el-form-item>

		<el-form-item label="必填" prop="delivery">
			<el-switch v-model="data.required"></el-switch>
		</el-form-item>

		<el-form-item label="下拉选项" prop="delivery">
			<div class="el-row" style="margin-bottom: 10px;" v-for="(item, index) in data.options" :key="index">
				<div class="el-col el-col-2"><span ></span></div>
				<div class="option-radio el-col el-col-4">
					<el-radio v-model="data.selected" :label="item.value" @change="selectRadio(index)">选中</el-radio>
				</div>
				<div class="el-col el-col-15">
					<el-input size="mini" v-model="item.text" placeholder="请输入值" @input="(e) => radioInputValue(e, index)"></el-input>
				</div>
				<div class="el-col el-col-1">
					<el-input type="hidden" size="mini" autocomplete="off" placeholder="请输入内容" v-model="item.value"></el-input>
				</div>
				<div class="option-delete el-col el-col-2" @click="deleteRadioItem(index)"><i class="el-icon-delete"></i></div>
			</div>
		
			<i class="el-icon-plus option-add-btn" @click="addRadioItem">新增</i>
		</el-form-item>
	</div>
</template>

<script>
	export default {
		data() {
			return {

			}
		},
		props: {
			data: {
				type: Object,
				default: function() {}
			},
		},
		methods: {
			addRadioItem(){
				var num = this.data.options.length, num = num+1, item = {
					text: '选项'+num,
					value:'选项'+num,
					selected: false
				}
				this.data.options = this.data.options.concat(item)
			},
			radioInputValue(value, index){
				var options = this.data.options
				options[index].value = value
				if(options[index].selected == true){
					this.data.selected = value
				}
			},
			selectRadio(index){
				var options = this.data.options
				for(var i = 0; i < options.length; i++){
					options[i].selected = false
				}
				options[index].selected = true
			},
			deleteRadioItem(index){
				this.data.options.splice(index, 1)
			}
		}
	}
</script>

<style>
	.option-delete{
		cursor: pointer;
	}
	.option-add-btn{
		cursor: pointer;
	}
</style>
