<template>
	<div class="my-container" style="display: flex;flex-direction: column;">
		<div class="el-row is-justify-space-between el-row--flex">
			<div class="el-col el-col-6 left">
				<div class="widget-cat">字段列表</div>
				<ul>
					<li class="widget-label" v-for="(item, index) in form_list" :key="index" @click="addFormField(item)">
						<i class="fa fa-info-circle"></i>
						<span>{{item.name}}</span>
					</li>
				</ul>
			</div>
			<div class="el-col el-col-8">
				<div class="page_center" id="view-page">
					<el-form label-position="right">
						<draggable v-model="myArray" @change="draggableChange">
							<transition-group>
								<div v-for="(element, index) in myArray" :key="element.field">
									<div class="diy" :class="select_index==index?'select': ''" @click="selectFormField(index)">
										<mtitle v-if="element.type=='title'" :content="element.value"></mtitle>
										<mdesc v-else-if="element.type=='desc'" :content="element.value"></mdesc>
										<minput v-else-if="element.type=='input'" :label_name="element.name" :required="element.required" :layout="element.layout"
										 :tips="element.tips"></minput>
										<mtextarea v-else-if="element.type=='textarea'" :label_name="element.name" :required="element.required"
										 :layout="element.layout" :tips="element.tips"></mtextarea>
										<mselect v-else-if="element.type=='select'" :label_name="element.name" :required="element.required" :layout="element.layout"
										 :tips="element.tips"></mselect>
										<mradio v-else-if="element.type=='radio'" :label_name="element.name" :required="element.required" :options="element.options"
										 :selected="element.selected"></mradio>
										<mcheckbox v-else-if="element.type=='checkbox'" :label_name="element.name" :required="element.required"
										 :options="element.options" :selected="element.selected"></mcheckbox>
										<mupload-img v-else-if="element.type=='uploadimg'" :label_name="element.name" :required="element.required"></mupload-img>
										<mupload-video v-else-if="element.type=='uploadvideo'" :label_name="element.name" :required="element.required"></mupload-video>
										<mupload-file v-else-if="element.type=='uploadfile'" :label_name="element.name" :required="element.required"></mupload-file>
										
										<i class="diy_delete el-icon-close" :style="'display:'+(select_index==index?'block': 'none')" @click.stop="deleteFormField(index)"></i>									
									</div>
								</div>
							</transition-group>
						</draggable>
					</el-form>
				</div>
			</div>
			<div class="el-col el-col-9 right">
				<el-form label-position="right" label-width="100px" v-if="select_index!=-1">
					<minput-attr v-if="myArray[select_index].type=='input'" :data="myArray[select_index]"></minput-attr>
					<mtitle-attr v-if="myArray[select_index].type=='title'" :data="myArray[select_index]"></mtitle-attr>
					<mdesc-attr v-if="myArray[select_index].type=='desc'" :data="myArray[select_index]"></mdesc-attr>
					<mtextarea-attr v-if="myArray[select_index].type=='textarea'" :data="myArray[select_index]"></mtextarea-attr>
					<mselect-attr v-if="myArray[select_index].type=='select'" :data="myArray[select_index]"></mselect-attr>
					<mradio-attr v-if="myArray[select_index].type=='radio'" :data="myArray[select_index]"></mradio-attr>
					<mcheckbox-attr v-if="myArray[select_index].type=='checkbox'" :data="myArray[select_index]"></mcheckbox-attr>
					<mupload-img-attr v-if="myArray[select_index].type=='uploadimg'" :data="myArray[select_index]"></mupload-img-attr>
					<mupload-video-attr v-if="myArray[select_index].type=='uploadvideo'" :data="myArray[select_index]"></mupload-video-attr>
					<mupload-file-attr v-if="myArray[select_index].type=='uploadfile'" :data="myArray[select_index]"></mupload-file-attr>
				</el-form>
			</div>
		</div>
		<el-col :span="24" class="designer-bottom">
			<el-button size="medium" type="infor" @click="back">返回</el-button>
			<el-button size="medium" type="success">保存并预览</el-button>
			<el-button size="medium" type="primary" @click="doSubmit">确认提交</el-button>
		</el-col>
	</div>
</template>

<script>
	import bus from '../../bus'
	import draggable from 'vuedraggable'

	import minput from './pages/Minput'
	import minputAttr from './attrs/Minput_attr'

	import mtitle from './pages/Mtitle'
	import mtitleAttr from './attrs/Mtitle_attr'

	import mdesc from './pages/Mdesc'
	import mdescAttr from './attrs/Mdesc_attr'

	import mtextarea from './pages/Mtextarea'
	import mtextareaAttr from './attrs/Mtextarea_attr'

	import mselect from './pages/Mselect'
	import mselectAttr from './attrs/Mselect_attr'

	import mradio from './pages/Mradio'
	import mradioAttr from './attrs/Mradio_attr'

	import mcheckbox from './pages/Mcheckbox'
	import mcheckboxAttr from './attrs/Mcheckbox_attr'

	import muploadImg from './pages/Mupload_img'
	import muploadImgAttr from './attrs/Mupload_img_attr'
	
	import muploadVideo from './pages/Mupload_video'
	import muploadVideoAttr from './attrs/Mupload_video_attr'
	
	import muploadFile from './pages/Mupload_file'
	import muploadFileAttr from './attrs/Mupload_file_attr'

	import * as config from './config/config'
	export default {
		name: "add-form-design",
		components: {
			draggable,
			minput,
			minputAttr,
			mtitle,
			mtitleAttr,
			mdesc,
			mdescAttr,
			mtextarea,
			mtextareaAttr,
			mselect,
			mselectAttr,
			mradio,
			mradioAttr,
			mcheckbox,
			mcheckboxAttr,
			muploadImg,
			muploadImgAttr,
			muploadVideo,
			muploadVideoAttr,
			muploadFile,
			muploadFileAttr
		},
		data() {
			return {
				select_index: -1, //选中的组件索引
				form_list: [], // 所有的组件列表
				myArray: [] //添加的组件
			}
		},
		created() {
			this.form_list = config.default
		},
		methods: {
			addFormField(item) {
				console.log(item)
				item.field = this.getId(5)
				this.myArray = this.myArray.concat(JSON.parse(JSON.stringify(item)))
			},
			selectFormField(index) {
				this.select_index = index

			},
			deleteFormField(index) {
				if (index == this.select_index) {
					this.select_index = -1
				}
				this.myArray.splice(index, 1)
			},
			draggableChange(e) {
				console.log(e)
				if (e.moved.oldIndex == this.select_index) {
					this.select_index = e.moved.newIndex
				}
			},
			draggableMove(e) {
				console.log(e)
			},
			back() {
				bus.$emit('close_current_tags');
			},
			doSubmit() {
				console.log(this.myArray)
			}
		}
	}
</script>

<style>
	.left .widget-cat {
		padding: 8px 0px;
		font-size: 13px;
		clear: both;
		text-align: left;
	}

	.left .widget-cat ul {
		position: relative;
		overflow: hidden;
		padding: 0 10px 10px;
		margin: 0;
	}

	.left ul li.widget-label {
		display: block;
		width: 48%;
		position: relative;
		float: left;
		left: 0;
		overflow: hidden;
		text-overflow: ellipsis;
		margin: 1%;
		color: #333;
		border: 1px solid #f4f6fc;
		font-size: 12px;
		line-height: 26px;
		white-space: nowrap;
		box-sizing: border-box;
		cursor: pointer;
		background: #f4f6fc;
		border: 1px solid #f4f6fc;
		padding-left: 10px;
	}

	.page_center {
		background-color: rgb(249, 250, 252);
		height: 600px;
		overflow: auto;
		border: 10px solid rgb(204, 204, 204);
		box-sizing: border-box;
		padding: 10px;
	}

	.right {
		background-color: rgb(249, 250, 252);
		overflow: auto;
		float: left;
		height: 600px;
		text-align: left;
		padding: 5px;
	}

	.designer-bottom {
		margin-top: 30px;
	}

	.diy {
		position: relative;
		margin-bottom: 10px;
	}

	.diy:hover {
		cursor: move;
		border: 1px solid #409eff !important;
		box-sizing: border-box;
	}

	.select {
		border: 1px solid #409eff !important;
		cursor: move;
	}

	.diy_delete {
		font-size: 12px;
		background-color: #409eff;
		height: 18px;
		width: 18px;
		position: absolute;
		right: 0;
		top: 0;
		text-align: center;
		z-index: 10;
		cursor: pointer;
		font-size: 14px;
		color: #fff;
		border-radius: 0 0 0 100%;
	}

	.right label {
		font-weight: 700;
	}
</style>
